import React, { useState } from "react";
import "../Projects.css";

const images = [];
for (let i = 1; i <= 12; i++) {
  images.push(require(`./image/${i}.png`));
}

function Project1() {
  const [currentIndex, setCurrentIndex] = useState(0);

  const goToNext = () => {
    setCurrentIndex((prevIndex) => (prevIndex + 1) % images.length);
  };

  const goToPrev = () => {
    setCurrentIndex((prevIndex) =>
      prevIndex === 0 ? images.length - 1 : prevIndex - 1
    );
  };

  return (
    <div className="project-container">
      <div className="project-header">
        <h2></h2>
        <p className="project-date">2024.02</p>
        <p>
          **<span style={{ color: "red" }}>HTML</span>**, **
          <span style={{ color: "red" }}>CSS</span>**, **
          <span style={{ color: "red" }}>JavaScript</span>**, **
          <span style={{ color: "red" }}>Spring Boot</span>**   
          .
        </p>
        <p>
           **<span style={{ color: "red" }}>CRUD</span>** 
          .
        </p>
      </div>

      <div className="project-content">
        <div className="project-left">
          <div className="image-slider">
            <p className="image-counter">
              {currentIndex + 1} / {images.length}
            </p>
            <button className="prev-button" onClick={goToPrev}>
              &#10094;
            </button>
            <img
              src={images[currentIndex]}
              alt={`  ${currentIndex + 1}`}
              className="project-image"
            />
            <button className="next-button" onClick={goToNext}>
              &#10095;
            </button>
          </div>
        </div>
        <div className="project-right">
          <h3> </h3>
          <div className="icon-container">
            <p>Eclipse</p>
            <p>
              **<span style={{ color: "red" }}>Java</span>**
            </p>
            <p>
              **<span style={{ color: "red" }}>MySQL</span>**
            </p>
            <p>
              **<span style={{ color: "red" }}>HTML</span>**
            </p>
            <p>
              **<span style={{ color: "red" }}>CSS</span>**
            </p>
            <p>
              **<span style={{ color: "red" }}>JavaScript</span>**
            </p>
            <p>
              **<span style={{ color: "red" }}>Hibernate</span>**
            </p>
            <p>
              **<span style={{ color: "red" }}>AWS</span>**
            </p>
            <p>
              **<span style={{ color: "red" }}>Spring Boot</span>**
            </p>
            <p>Gradle</p>
            <p>GitHub</p>
            <p>TypeScript</p>
          </div>

          <h3>  </h3>
          <div className="problem-solving">
            <ul>
              <li>
                **<span style={{ color: "red" }}>JDBC</span>**  
                   ,   **
                <span style={{ color: "red" }}>Spring</span>** 
                .  **
                <span style={{ color: "red" }}>Spring</span>** , **
                <span style={{ color: "red" }}>Spring Boot</span>** 
                   .
              </li>
              <li>
                **<span style={{ color: "red" }}>Spring Boot</span>** **
                <span style={{ color: "red" }}>MVC </span>**, **
                <span style={{ color: "red" }}>Controller</span>**, **
                <span style={{ color: "red" }}>Entity</span>**   
                 ,     
                .
              </li>
              <li>
                **<span style={{ color: "red" }}>SQL</span>**   
                 ,      , 
                  .
              </li>
              <li>
                **<span style={{ color: "red" }}></span>** **
                <span style={{ color: "red" }}>AWS</span>**  
                ,        .
              </li>
              <li>
                 **<span style={{ color: "red" }}></span>**
                 ,   **
                <span style={{ color: "red" }}> </span>**  
                 .
              </li>
            </ul>
          </div>

          <h3>   </h3>
          <div className="improvement">
            <ul>
              <li>
                **<span style={{ color: "red" }}>Java</span>** **
                <span style={{ color: "red" }}>Spring Boot</span>** 
                , **<span style={{ color: "red" }}>Spring Boot</span>**
                      .  
                 **<span style={{ color: "red" }}>Spring Boot</span>**
                  .
              </li>
              <li>
                **<span style={{ color: "red" }}>SQL</span>** 
                 ,       
                 .
              </li>
              <li>
                **<span style={{ color: "red" }}>AWS</span>**  ,
                **<span style={{ color: "red" }}> </span>** 
                  .
              </li>
              <li>
                 **<span style={{ color: "red" }}> </span>
                **  ,      
                  .
              </li>
            </ul>
          </div>

          <h3>GitHub</h3>
          <a
            href="https://github.com/pjh4554/sbb"
            target="_blank"
            rel="noopener noreferrer"
            className="github-link"
          >
            https://github.com/pjh4554/sbb
          </a>
        </div>
      </div>
    </div>
  );
}

export default Project1;
