import React, { useState } from "react";
import "../Projects.css";

const images = [];
for (let i = 1; i <= 11; i++) {
  images.push(require(`./image/${i}.jpg`));
}

function ProjectTemplate() {
  const [currentIndex, setCurrentIndex] = useState(0);

  const goToNext = () => {
    setCurrentIndex((prevIndex) => (prevIndex + 1) % images.length);
  };

  const goToPrev = () => {
    setCurrentIndex((prevIndex) =>
      prevIndex === 0 ? images.length - 1 : prevIndex - 1
    );
  };

  return (
    <div className="project-container">
      <div className="project-header">
        <h2>Crafty</h2> {/*   */}
        <p className="project-date">2024-09</p> {/*  */}
        <p>
          **<span style={{ color: "red" }}>Flutter</span>** **
          <span style={{ color: "red" }}>Dart</span>**    
           . **<span style={{ color: "red" }}>Firebase</span>** 
           ,  **
          <span style={{ color: "red" }}>Pixso</span>** . 
           **<span style={{ color: "red" }}>Google Play Store</span>
          ** .
        </p>
        <p>
          ,   **
          <span style={{ color: "red" }}>Google Ads</span>**  
               .    
               ,    
            .      , 
            (:  ) .
        </p>
      </div>

      <div className="project-content">
        <div className="project-left">
          <div className="image-slider">
            <p className="image-counter">
              {currentIndex + 1} / {images.length}
            </p>
            <button className="prev-button" onClick={goToPrev}>
              &#10094;
            </button>
            <img
              src={images[currentIndex]}
              alt={`  ${currentIndex + 1}`}
              className="project-image"
            />
            <button className="next-button" onClick={goToNext}>
              &#10095;
            </button>
          </div>
        </div>
        <div className="project-right">
          <h3> </h3>
          <div className="icon-container">
            {/*   */}
            <p>Flutter</p>
            <p>Dart</p>
            <p>Firebase</p>
            <p>Pixso</p>
            <p>Google Play Store</p>
            <p>Google Ads</p> {/* Google Ads   */}
            <p>Android Studio</p>
            <p>Github</p>
          </div>

          <h3>  </h3>
          <div className="problem-solving">
            <ul>
              <li>
                **<span style={{ color: "red" }}>Flutter</span>** **
                <span style={{ color: "red" }}>Dart</span>**   
                ,  **<span style={{ color: "red" }}>Java</span>**
                     .
              </li>
              <li>
                **<span style={{ color: "red" }}>Firebase</span>** 
                   ,  **
                <span style={{ color: "red" }}> </span>** 
                ,     .
              </li>
              <li>
                  **<span style={{ color: "red" }}>Pixso</span>**
                    UI  .
              </li>
              <li>
                **<span style={{ color: "red" }}>Google Ads</span>**  
                  ,      
                .
              </li>
            </ul>
          </div>

          <h3>   </h3>
          <div className="improvement">
            <ul>
              <li>
                **<span style={{ color: "red" }}>UI </span>** 
                   .
              </li>
              <li>
                **<span style={{ color: "red" }}>Firebase</span>**  
                   ,   .
              </li>
              <li>
                **<span style={{ color: "red" }}>Flutter  </span>**
                     .
              </li>
              <li>
                **<span style={{ color: "red" }}>Google Ads</span>** 
                     .
              </li>
            </ul>
          </div>

          <h3>GitHub</h3>
          <a
            href="https://github.com/pjh4554/crafty"
            target="_blank"
            rel="noopener noreferrer"
            className="github-link"
          >
            https://github.com/pjh4554/crafty
          </a>
        </div>
      </div>
    </div>
  );
}

export default ProjectTemplate;
