import React, { useState } from "react";
import "../Projects.css";

const images = [];
for (let i = 1; i <= 7; i++) {
  images.push(require(`./image/${i}.png`));
}

function Project1() {
  const [currentIndex, setCurrentIndex] = useState(0);

  const goToNext = () => {
    setCurrentIndex((prevIndex) => (prevIndex + 1) % images.length);
  };

  const goToPrev = () => {
    setCurrentIndex((prevIndex) =>
      prevIndex === 0 ? images.length - 1 : prevIndex - 1
    );
  };

  return (
    <div className="project-container">
      <div className="project-header">
        <h2>  </h2>
        <p className="project-date">2023.11</p>
        <p>
          Python (pandas, pytimekr)   , , 
             .
        </p>
      </div>

      <div className="project-content">
        <div className="project-left">
          <div className="image-slider">
            <p className="image-counter">
              {currentIndex + 1} / {images.length}
            </p>
            <button className="prev-button" onClick={goToPrev}>
              &#10094;
            </button>
            <img
              src={images[currentIndex]}
              alt={`  ${currentIndex + 1}`}
              className="project-image"
            />
            <button className="next-button" onClick={goToNext}>
              &#10095;
            </button>
          </div>
        </div>
        <div className="project-right">
          <h3> </h3>
          <div className="icon-container">
            <p>Python</p>
            <p>VSCode</p>
          </div>

          <h3>  </h3>
          <div className="problem-solving">
            <ul>
              <li>
                    **
                <span style={{ color: "red" }}> </span>** 
                .
              </li>
              <li>
                Python    (DataFrame) .
              </li>
              <li>
                **<span style={{ color: "red" }}> </span>** 
                 ,     .
              </li>
              <li>
                       ,
                   .  **
                <span style={{ color: "red" }}> </span>** 
                .
              </li>
            </ul>
          </div>

          <h3>   </h3>
          <div className="improvement">
            <ul>
              <li>
                   , **
                <span style={{ color: "red" }}>  </span>**
                .
              </li>
              <li>
                      , **
                <span style={{ color: "red" }}>   </span>** 
                .
              </li>
              <li>
                **<span style={{ color: "red" }}></span>**  
                 .       
                 .
              </li>
              <li>
                    , **
                <span style={{ color: "red" }}></span>** 
                .     
                  .
              </li>
            </ul>
          </div>

          <h3>GitHub</h3>
          <a
            href="https://github.com/pjh4554/conference-python-"
            target="_blank"
            rel="noopener noreferrer"
            className="github-link"
          >
            https://github.com/pjh4554/conference-python-
          </a>
        </div>
      </div>
    </div>
  );
}

export default Project1;
